<template>
  <div class="home">
    <header>
      <div class="search">
        <img class="search_img1" src="../../assets/images/icon_vip@2x.png" alt />
        <input type="text" @focus="onSearch()" />
        <img
          @click="setCollectShow=!setCollectShow"
          class="search_img2"
          src="../../assets/images/icon_more1@2x.png"
          alt
        />
        <img class="search_img3" src="../../assets/images/icon_search@2x.png" alt />
        <div class="time">{{new Date().getDate()}}</div>
        <ul v-show="setCollectShow">
          <p></p>
          <li @click="onSetCollect">
            <img src="../../assets/images/essay_btn_collect_gray@2x.png" alt />
            <span>我的关注</span>
          </li>
        </ul>
      </div>
      <div class="swiper">
        <van-swipe :autoplay="3000" indicator-color="#fff">
          <van-swipe-item v-for="(item,index) of headSwiper" :key="index">
            <a :href="item.imagebarurl">
              <img :src="item.icon" alt />
            </a>
          </van-swipe-item>
        </van-swipe>
      </div>
    </header>
    <nav>
      <div class="nav">
        <transition enter-active-class="animated fadeInDown">
          <div class="attention_box" v-show="foldFlag">
            <div class="attention" @click="onSwitchover()">
              <img
                :src="navAttention==true?require('../../assets/images/icon_follow@2x.png'):require('../../assets/images/icon_history@2x.png')"
                alt
              />
            </div>
          </div>
        </transition>
        <transition enter-active-class="animated fadeInDown">
          <div class="nav_tab1" v-show="foldFlag">
            <div class="person-wrap" ref="personWrap">
              <ul class="person-list" ref="personTab">
                <li
                  ref="personItem"
                  v-for="(item,index) of navAttention==true?navAttentionArr:navFootprintArr"
                  :key="index"
                  @click="onSectionView(item)"
                >
                  <div>
                    <img :src="item.icon" alt />
                  </div>
                  <p>{{item.shortname}}</p>
                  <em v-show="item.updateShow"></em>
                </li>
                <li ref="personItem" @click="onConcerns()">
                  <div>
                    <img src="../../assets/images/icon_more@2x.png" alt />
                  </div>
                  <p>全部</p>
                </li>
              </ul>
            </div>
          </div>
        </transition>
        <transition enter-active-class="animated fadeInUp">
          <div class="nav_tab" v-show="!foldFlag">
            <div class="title">
              <p @click="navAttention = true" :class="{nav_tab_active:navAttention==true}">关注</p>
              <p
                @click="navAttention = false,getNavFootprintData();"
                :class="{nav_tab_active:navAttention==false}"
              >足迹</p>
            </div>
            <ul>
              <li
                ref="nav_tab"
                v-for="(item,index) of navAttention==true?navAttentionArr:navFootprintArr"
                :key="index"
                v-show="index<7"
                @click="onSectionView(item)"
              >
                <div>
                  <img :src="item.icon" alt />
                </div>
                <p>{{item.shortname}}</p>
                <em v-show="item.updateShow"></em>
              </li>
              <li @click="onConcerns()">
                <div>
                  <img src="../../assets/images/icon_more@2x.png" alt />
                </div>
                <p>全部</p>
              </li>
            </ul>
          </div>
        </transition>
      </div>
      <transition enter-active-class="animated fadeInUp">
        <div class="fold" v-show="foldShow">
          <div class="triangle" @click="onNavTabView()">
            <p>{{foldFlag==true?"展开":"收起"}}</p>
            <img
              :src="foldFlag==true?require('../../assets/images/icon_retrac1@2x.png'):require('../../assets/images/icon_retrac@2x.png')"
              alt
            />
          </div>
        </div>
      </transition>
    </nav>
    <section>
      <div class="sec_recommend">
        <div class="rec_headline">
          <div class="logo">
            <img src="../../assets/images/pic_zsbb@2x.png" alt />
          </div>
          <div class="swiper">
            <van-swipe style="height: 46px;" :autoplay="3000" vertical :show-indicators="false">
              <van-swipe-item v-for="(item,index) of secSwiper" :key="index">
                <p @click="onBroadcast(item)">
                  <span>{{item.broadcasttype}}</span>
                  {{item.broadcastname}}
                </p>
              </van-swipe-item>
            </van-swipe>
          </div>
        </div>
        <div class="rec_content">
          <div class="rec_business" @click="onRecommendOne">
            <div>
              <p>{{secRecommendOne.shortcutbarname}}</p>
              <p>{{secRecommendOne.shortcutbardescript}}</p>
            </div>
            <img :src="secRecommendOne.shortcutImage" alt />
          </div>
          <div class="rec_feature" @click="onRecommendTwo">
            <p>{{secRecommendTwo.shortcutbarname}}</p>
            <img :src="secRecommendTwo.shortcutImage" alt />
          </div>
          <div class="rec_incident" @click="onRecommendThree">
            <p>{{secRecommendThree.shortcutbarname}}</p>
            <img :src="secRecommendThree.shortcutImage" alt />
          </div>
        </div>
      </div>
      <div class="sec_tab">
        <van-sticky :offset-top="50" z-index="200">
          <div class="secTab" id="boxFixed" :class="{'is_fixed' : isFixed}">
            <ul>
              <li
                @click="onSectionViewTab(item,index)"
                :class="{sectionActive:index===secTabActive}"
                v-for="(item,index) of secTabContent"
                :key="index"
              >{{item}}</li>
            </ul>
            <img
              class="switch_style"
              :src="tabSection==true?require('../../assets/images/icon_modular@2x.png'):require('../../assets/images/icon_form@2x.png')"
              alt
              @click="onSecTabView()"
            />
          </div>
        </van-sticky>
        <div class="tab_section" id="secTabCon" v-show="tabSection">
          <div class="tab_content" v-for="(item,index) of secTabContentAllOne" :key="index">
            <div class="img_top_box">
              <img @click="onSectionView(item)" class="img_top" attempt="4" v-lazy="item.image" alt />
              <div class="fwl">{{item.tel}}</div>
            </div>
            <div class="title">
              <img :src="item.icon" alt />
              <p>
                <span>
                  {{item.title}}
                  <em v-show="item.updateShow"></em>
                </span>
              </p>
            </div>
            <div class="bottom">
              <div class="bo_l">
                <img
                  @click="onCollect(item)"
                  :src="item.identifySub.shadeImg==false?require('../../assets/images/essay_btn_collect_gray@3x.png'):require('../../assets/images/btn_collect_selected@3x.png')"
                  alt
                />
                <div @click="onLike($event,item)">
                  <span v-show="likeFlag"></span>
                </div>
                <p>{{item.sureCount}}</p>
              </div>
              <ul class="bo_r" @click="onOpinion(item)">
                <li></li>
                <li></li>
                <li></li>
              </ul>
            </div>
            <transition enter-active-class="animated zoomIn">
              <div
                class="trample"
                v-show="item.identifySub.trampleShow"
                @click="item.identifySub.trampleShow=false"
              >
                <p>
                  <span>相似视图</span>
                  <img src="../../assets/images/icon_tiaozhuanlb@3x.png" alt />
                </p>
                <p @click="item.identifySub.trampleShow = false,writeShow = true,secConObj=item">
                  <span>其他意见</span>
                  <img src="../../assets/images/icon_xieyijian@3x.png" alt />
                </p>
                <p @click="onTreadCause($event,item)">
                  <span>不感兴趣</span>
                  <img src="../../assets/images/icon_kulian@3x.png" alt />
                </p>
              </div>
            </transition>
          </div>
        </div>
        <div class="tab_section1" id="secTabCon1" v-show="!tabSection">
          <div class="tab_content" v-for="(item,index) of secTabContentAllOne" :key="index">
            <div class="left">
              <img @click="onSectionView(item)" attempt="4" v-lazy="item.image" alt />
              <div class="fwl">{{item.tel}}</div>
            </div>
            <div class="right">
              <div @click="onSectionView(item)" class="title">
                <img :src="item.icon" alt />
                <span>
                  {{item.title}}
                  <em v-show="item.updateShow"></em>
                </span>
              </div>
              <p @click="onSectionView(item)">{{item.text}}</p>
              <div class="bottom">
                <img
                  @click="onCollect(item)"
                  :src="item.identifySub.shadeImg==false?require('../../assets/images/essay_btn_collect_gray@3x.png'):require('../../assets/images/btn_collect_selected@3x.png')"
                  alt
                />
                <div @click="onLike($event,item)">
                  <span v-show="likeFlag"></span>
                </div>
                <ol class="tel">{{item.sureCount}}</ol>
                <ul class="bo_r" @click="onOpinion(item)">
                  <li></li>
                  <li></li>
                  <li></li>
                </ul>
              </div>
            </div>
            <transition enter-active-class="animated zoomIn">
              <div
                class="trample"
                v-show="item.identifySub.trampleShow"
                @click="item.identifySub.trampleShow=false"
              >
                <p>
                  <span>相似视图</span>
                  <img src="../../assets/images/icon_tiaozhuanlb@3x.png" alt />
                </p>
                <p @click="item.identifySub.trampleShow = false,writeShow = true,secConObj=item">
                  <span>其他意见</span>
                  <img src="../../assets/images/icon_xieyijian@3x.png" alt />
                </p>
                <p @click="onTreadCause($event,item)">
                  <span>不感兴趣</span>
                  <img src="../../assets/images/icon_kulian@3x.png" alt />
                </p>
              </div>
            </transition>
          </div>
        </div>
      </div>
    </section>
    <van-overlay :show="writeShow" z-index="300">
      <div class="submitInfo" @click.stop>
        <van-field
          v-model="treadMsg"
          rows="6"
          autosize
          type="textarea"
          maxlength="100"
          placeholder="请输入具体意见描述"
          show-word-limit
        />
        <van-button
          type="primary"
          color="linear-gradient(270deg, #45A4B1 0%, #2A7875 100%)"
          block
          @click="onTreadIdea()"
        >提交</van-button>
        <img
          @click="writeShow = false,treadMsg = ''"
          src="../../assets/images/btn_close@1x.png"
          alt
        />
      </div>
    </van-overlay>
    <tabbar></tabbar>
    <!-- <router-link to="/mine">
      <div class="goMine" style="width:1rem;height:0.5rem;background:#000;color:#fff">go我的</div>
    </router-link> -->
  </div>
</template>
<script>
import BScroll from "better-scroll";
import axios from "axios";
const productionUrl =
  window.location.href.split(":")[0] + "://" + document.domain + ":8080/mobile";
const instance = axios.create({
  baseURL:
    process.env.NODE_ENV === "development" ? "/api/mobile" : productionUrl,
});
export default {
  name: "Home",
  components: {
    Tabbar: () => import("../../components/Tabbar.vue"),
  },
  data() {
    return {
      setCollectShow: false,
      navAttention: true,
      foldFlag: true,
      foldShow: true,
      tabSection: true,
      secTabActive: 0,
      secConObj: {},
      writeShow: false,
      treadMsg: "",
      navAttentionArr: [],
      navFootprintArr: [],
      headSwiper: [],
      secRecommendOne: {},
      secRecommendTwo: {},
      secRecommendThree: {},
      secSwiper: [],
      likeFlag: false,
      secTabContentAllOne: [],
      secTabContentAll: [],
      secTabContent: [],
      userInfoObj: {},
      timer: "",
      isFixed: false,
      offsetTop: 0,
      secTabConTop: 0,
      scrollTop: 0,
    };
  },
  created() {
    window.addEventListener("scroll", this.handleScroll, true);
    this.$nextTick(() => {
      this.ifLoginEquipment(); //判断登录机型函数
      // this.offsetTop = document.querySelector('#boxFixed').offsetTop;
    });
  },
  beforeDestroy() {
    clearTimeout(this.timer);
    window.removeEventListener("scroll", this.handleScroll);
  },
  methods: {
    handleScroll() {
      // 设置或获取位于对象最顶端和窗口中可见内容的最顶端之间的距离 (被卷曲的高度)
      this.scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      //如果被卷曲的高度大于吸顶元素到顶端位置 的距离
      // this.isFixed = this.scrollTop > this.offsetTop ? true : false;
    },
    //视图一分钟更新一次
    viewUpdateTime() {
      this.timer = setTimeout(() => {
        clearTimeout(this.timer);
        this.getHeadSwiper(); //header轮播
        this.getNavAttentionData(); //nav关注
        this.getBroadcast(); //掌上播报
        this.getSectionRecommend(); //业务展示数据
        this.getSectionView(); //section数据
        this.viewUpdateTime(); //视图一分钟更新一次
      }, 60000);
    },
    //判断登录机型函数
    ifLoginEquipment() {
      var EquipmentMsg = navigator.userAgent;
      var TogoFlag = 0;
      var Agents = [
        "Android",
        "iPhone",
        "SymbianOS",
        "Windows Phone",
        "iPad",
        "iPod",
      ];
      for (var i = 0; i < Agents.length; i++) {
        if (EquipmentMsg.indexOf(Agents[i]) > 0) {
          TogoFlag = 1;
          break;
        }
      }
      if (TogoFlag == 1) {
        this.getUserInfo(); //获取用户信息
      } else {
        this.$router.push({ path: "/error", query: {} });
      }
    },
    //获取用户信息
    getUserInfo() {
      var resultObj = {
        USER_MOBILE: "18610299680",
        USER_CODE: "0001151515",
        USER_NAME: "楚慧静",
        USER_POST: "经理(一级) ",
        USER_EMAIL: "楚慧静V系统二部V数据中心VICBC",
        DEPT_CODE: "0010101136",
        USER_SEX: "2",
        CMPY_NAME: "工商银行",
        ODEPT_CODE: "0010100003",
        USER_OFFICE_PHONE: "010- 82988545",
        ODEPT_CODE_PATH: "0010100000^0010100003^",
        USER_ICON: "",
        ADMIN_GROUP_CODES: 1,
        CMPY_ADMIN: "",
        TDEPT_NAME: "数据中心本部",
        USER_BIRTHDAY: "19930214",
        USER_LOGIN_NAME: "001151515",
      };
      let userInfoObj = {};
      let odeptidArr = resultObj.ODEPT_CODE_PATH.split("^");
      userInfoObj.aamid = resultObj.USER_LOGIN_NAME;
      userInfoObj.deptid = resultObj.DEPT_CODE;
      userInfoObj.odeptid = odeptidArr[odeptidArr.length - 2];
      userInfoObj.user_mobile = resultObj.USER_MOBILE;
      userInfoObj.user_name = resultObj.USER_NAME;
      userInfoObj.user_post = resultObj.USER_POST;
      userInfoObj.user_email = resultObj.USER_EMAIL;
      userInfoObj.user_sex = resultObj.USER_SEX;
      userInfoObj.cmpy_name = resultObj.CMPY_NAME;
      userInfoObj.user_office_phone = resultObj.USER_OFFICE_PHONE;
      userInfoObj.tdept_name = resultObj.TDEPT_NAME;
      userInfoObj.user_birthday = resultObj.USER_BIRTHDAY;
      this.$store.commit("userInfo", userInfoObj);
      this.userInfoObj = this.$store.state.userInfoObj;
      setTimeout(() => {
        this.browseRecord(); //浏览记录
        this.getHeadSwiper(); //header轮播
        this.getNavAttentionData(); //nav关注
        this.getBroadcast(); //掌上播报
        this.getSectionRecommend(); //业务展示数据
        this.getSectionView(); //section数据
        this.viewUpdateTime(); //视图一分钟更新一次
      }, 0);
      setTimeout(() => {
        var that = this;
        window["rh"] &&
          window["rh"].getUserInfo(
            {},
            function (result) {
              var resultObj = JSON.parse(result);
              let userInfoObj = {};
              let odeptidArr = resultObj.ODEPT_CODE_PATH.split("^");
              userInfoObj.aamid = resultObj.USER_LOGIN_NAME;
              userInfoObj.deptid = resultObj.DEPT_CODE;
              userInfoObj.odeptid = odeptidArr[odeptidArr.length - 2];
              userInfoObj.user_mobile = resultObj.USER_MOBILE;
              userInfoObj.user_name = resultObj.USER_NAME;
              userInfoObj.user_post = resultObj.USER_POST;
              userInfoObj.user_email = resultObj.USER_EMAIL;
              userInfoObj.user_sex = resultObj.USER_SEX;
              userInfoObj.cmpy_name = resultObj.CMPY_NAME;
              userInfoObj.user_office_phone = resultObj.USER_OFFICE_PHONE;
              userInfoObj.tdept_name = resultObj.TDEPT_NAME;
              userInfoObj.user_birthday = resultObj.USER_BIRTHDAY;
              that.$store.commit("userInfo", userInfoObj);
              that.userInfoObj = that.$store.state.userInfoObj;
              setTimeout(() => {
                that.browseRecord(); //浏览记录
                that.getHeadSwiper(); //header轮播
                that.getNavAttentionData(); //nav关注
                that.getBroadcast(); //掌上播报
                that.getSectionRecommend(); //业务展示数据
                that.getSectionView(); //section数据
                that.viewUpdateTime(); //视图一分钟更新一次
              }, 0);
            },
            function (err) {
              alert(err);
            }
          );
      }, 100);
    },
    //浏览记录
    browseRecord() {
      this.$axios.post("/hopeUserLog_h/insertFourCh", {
        aamid: this.userInfoObj.aamid,
        moduleid: -1,
        logtime: new Date(),
      });
    },
    //查询视图推荐
    viewRecommend() {
      this.$axios.post("/hopeRecommed/queryRecommedModule", {}).then((res) => {
        let arrMsg = [];
        let tabbarFlag = 1;
        if (res.data.data === null) {
          let numLen = this.secTabContentAll.length;
          let num = Math.floor(Math.random() * numLen);
          arrMsg.push(this.secTabContentAll[num]);
          setTimeout(() => {
            window.localStorage.setItem(
              "viewRecommend",
              JSON.stringify(arrMsg)
            );
            this.$store.commit("tabbarFlagfn", tabbarFlag);
          }, 0);
        } else {
          for (i of res.data.data) {
            arrMsg = this.secTabContentAll.filter((item) => {
              return i.moduleid == item.moduleid;
            });
            if (arrMsg.length == 1) {
              break;
            }
          }
          setTimeout(() => {
            window.localStorage.setItem(
              "viewRecommend",
              JSON.stringify(arrMsg)
            );
            this.$store.commit("tabbarFlagfn", tabbarFlag);
          }, 0);
        }
      });
    },
    //nav滚动条
    personScroll() {
      this.$nextTick(() => {
        let width = (this.$refs.personItem.length + 1) * 80;
        this.$refs.personTab.style.width = width + "px";
        if (!this.scroll) {
          this.scroll = new BScroll(this.$refs.personWrap, {
            startX: 0,
            click: true,
            scrollX: true,
            // 忽略竖直方向的滚动
            scrollY: false,
            eventPassthrough: "vertical",
          });
        } else {
          this.scroll.refresh();
        }
      });
    },
    //点击搜索栏
    onSearch() {
      this.$router.push({ path: "/search" });
      this.setCollectShow = false;
    },
    //点击设置关注
    onSetCollect() {
      this.$router.push({ path: "/myConcerns" });
    },
    //获取header轮播数据
    getHeadSwiper() {
      this.$axios
        .post("/hopeImagebar/queryHopeImagebar", {
          aamid: this.userInfoObj.aamid,
          deptid: this.userInfoObj.deptid,
          odeptid: this.userInfoObj.odeptid,
        })
        .then((res) => {
          if (res.status === 200 && res.data.status === "000000") {
            [...res.data.data].forEach((item) => {
              if (item.imagebarurl === null || item.imagebarurl === "") {
                item.imagebarurl = "javascript:void(0)";
              }
            });
            this.headSwiper = [...res.data.data] || [];
          }
        });
    },
    //获取nav关注数据
    getNavAttentionData() {
      this.$axios
        .post("/hopeModule/queryWatchHopeModule", {
          aamid: this.userInfoObj.aamid,
        })
        .then((res) => {
          if (res.status === 200 && res.data.status === "000000") {
            let attentionList = [...res.data.data.list].sort((a, b) => {
              return b.sequence - a.sequence;
            });
            attentionList.forEach((i) => {
              i.title = i.modulename;
            });
            this.getDataUpdate(attentionList,0);
            this.navAttentionArr = attentionList || [];
            this.foldFlag = res.data.data.mstatus;
            let timer = setTimeout(() => {
              if (timer) {
                clearTimeout(timer);
                this.personScroll();
                if (this.foldFlag === true) {
                  this.$refs.personItem.length == null ||
                  this.$refs.personItem.length <= 3
                    ? (this.foldShow = false)
                    : (this.foldShow = true);
                } else {
                  this.foldShow = true;
                }
              }
            }, 0);
          }
        });
    },
    //点击获取nav足迹数据
    getNavFootprintData() {
      this.$axios
        .post("/hopeModule/queryMyFoot", {
          aamid: this.userInfoObj.aamid,
          status: "1",
        })
        .then((res) => {
          if (res.status === 200 && res.data.status === "000000") {
            let FootList = [...res.data.data].sort((a, b) => {
              return b.footTime - a.footTime;
            });
            this.getDataUpdate(FootList,0);
            this.navFootprintArr = FootList || [];
            let timer = setTimeout(() => {
              if (timer) {
                clearTimeout(timer);
                this.personScroll();
                if (this.foldFlag === true) {
                  this.$refs.personItem.length == null ||
                  this.$refs.personItem.length <= 3
                    ? (this.foldShow = false)
                    : (this.foldShow = true);
                } else {
                  this.foldShow = true;
                }
              }
            }, 0);
          }
        });
    },
    //点击切换nav关注和足迹
    onSwitchover() {
      this.navAttention = !this.navAttention;
      if (this.navAttention === false) {
        this.getNavFootprintData();
      }
      let timer = setTimeout(() => {
        if (timer) {
          clearTimeout(timer);
          this.personScroll();
          if (this.foldFlag === true) {
            this.$refs.personItem.length == null ||
            this.$refs.personItem.length <= 3
              ? (this.foldShow = false)
              : (this.foldShow = true);
          } else {
            this.foldShow = true;
          }
        }
      }, 0);
    },
    //点击展开或收起
    onNavTabView() {
      this.foldFlag = !this.foldFlag;
      this.$axios
        .post("/hopeModule/editModuleConf", {
          aamid: this.userInfoObj.aamid,
          confvalue: this.foldFlag === true ? 0 : 1,
          setname: "关注",
        })
        .then((res) => {
          if (res.status === 200 && res.data.status === "000000") {
            let timer = setTimeout(() => {
              if (timer) {
                clearTimeout(timer);
                this.personScroll();
                if (this.foldFlag === true) {
                  this.$refs.personItem.length == null ||
                  this.$refs.personItem.length <= 3
                    ? (this.foldShow = false)
                    : (this.foldShow = true);
                } else {
                  this.foldShow = true;
                }
              }
            }, 0);
          }
        });
    },
    //nav关注或足迹更多
    onConcerns() {
      if (this.navAttention == true) {
        this.$router.push({ path: "/myConcerns" });
      } else {
        if (this.navFootprintArr.length > 0) {
          this.$router.push({ path: "/myFootprint" });
        } else {
          return false;
        }
      }
    },
    //点击掌上播报
    onBroadcast(item) {
      this.$router.push({
        path: "/broadcast",
        query: { name: item.broadcasttype },
      });
    },
    //获取掌上播报数据
    getBroadcast() {
      this.$axios
        .post("/hopeBroadcast/queryHopeBroadcast", {
          aamid: this.userInfoObj.aamid,
          deptid: this.userInfoObj.deptid,
          odeptid: this.userInfoObj.odeptid,
        })
        .then((res) => {
          if (res.status === 200 && res.data.status === "000000") {
            this.secSwiper = [...res.data.data] || [];
          }
        });
    },
    //点击业务展示1
    onRecommendOne() {
      if (
        this.secRecommendOne.shortModuleUrl != null &&
        this.secRecommendOne.shortModuleUrl != ""
      ) {
        window.location.href = this.secRecommendOne.shortModuleUrl;
      } else if (
        (this.secRecommendOne.shortModuleUrl == null ||
          this.secRecommendOne.shortModuleUrl == "") &&
        this.secRecommendOne.list != null &&
        this.secRecommendOne.list != ""
      ) {
        this.$router.push({
          path: "/recommenddetail",
          query: {
            id: this.secRecommendOne.list,
            title: this.secRecommendOne.shortcutbarname,
          },
        });
      } else {
        return false;
      }
    },
    //点击业务展示2
    onRecommendTwo() {
      if (
        this.secRecommendTwo.shortModuleUrl != null &&
        this.secRecommendTwo.shortModuleUrl != ""
      ) {
        window.location.href = this.secRecommendTwo.shortModuleUrl;
      } else if (
        (this.secRecommendTwo.shortModuleUrl == null ||
          this.secRecommendTwo.shortModuleUrl == "") &&
        this.secRecommendTwo.list != null &&
        this.secRecommendTwo.list != ""
      ) {
        this.$router.push({
          path: "/recommenddetail",
          query: {
            id: this.secRecommendTwo.list,
            title: this.secRecommendTwo.shortcutbarname,
          },
        });
      } else {
        return false;
      }
    },
    //点击业务展示3
    onRecommendThree() {
      if (
        this.secRecommendThree.shortModuleUrl != null &&
        this.secRecommendThree.shortModuleUrl != ""
      ) {
        window.location.href = this.secRecommendThree.shortModuleUrl;
      } else if (
        (this.secRecommendThree.shortModuleUrl == null ||
          this.secRecommendThree.shortModuleUrl == "") &&
        this.secRecommendThree.list != null &&
        this.secRecommendThree.list != ""
      ) {
        this.$router.push({
          path: "/recommenddetail",
          query: {
            id: this.secRecommendThree.list,
            title: this.secRecommendThree.shortcutbarname,
          },
        });
      } else {
        return false;
      }
    },
    //获取业务展示数据
    getSectionRecommend() {
      this.$axios
        .post("/hopeShortcutBar/queryShortcutBar", {
          aamid: this.userInfoObj.aamid,
          deptid: this.userInfoObj.deptid,
          odeptid: this.userInfoObj.odeptid,
        })
        .then((res) => {
          if (res.status === 200 && res.data.status === "000000") {
            this.secRecommendOne = res.data.data.list[0];
            this.secRecommendTwo = res.data.data.list[1];
            this.secRecommendThree = res.data.data.list[2];
          }
        });
    },
    //section关注和取消关注
    onCollect(item, e) {
      item.identifySub.shadeImg = !item.identifySub.shadeImg;
      if (item.identifySub.shadeImg == false) {
        this.$toast({
          message: "取消关注",
          icon: require("../../assets/images/btn_collect_normal@2x.png"),
        });
        this.$axios
          .post("/hopeModule/deleteWatchHopeModule", {
            aamid: this.userInfoObj.aamid,
            moduleid: item.moduleid,
          })
          .then((res) => {
            if (res.status === 200 && res.data.status === "000000") {
              this.getNavAttentionData();
            }
          });
      } else {
        this.$toast({
          message: "已关注",
          icon: require("../../assets/images/btn_collect_selected@2x.png"),
        });
        this.$axios
          .post("/hopeModule/insertWatchHopeModule", {
            aamid: this.userInfoObj.aamid,
            moduleid: item.moduleid,
          })
          .then((res) => {
            if (res.status === 200 && res.data.status === "000000") {
              this.getNavAttentionData();
            }
          });
      }
    },
    //section点赞
    onLike(e, item) {
      this.likeFlag = true;
      e.target.className = this.likeFlag ? "like_animate" : "";
      var timer = setTimeout(() => {
        clearTimeout(timer);
        this.likeFlag = false;
        e.target.className = this.likeFlag ? "like_animate" : "";
      }, 600);
      item.sureCount++;
      this.$axios.post("/hopeComments/insertHopeComments", {
        aamid: this.userInfoObj.aamid,
        moduleid: item.moduleid,
        logtime: Date.parse(new Date()),
        comments: 1,
        description: "",
      });
    },
    //点击三个点显示遮罩
    onOpinion(item) {
      this.secTabContentAllOne.forEach((i) => {
        i.identifySub.trampleShow = false;
      });
      item.identifySub.trampleShow = true;
    },
    //section点踩原因
    onTreadCause(e, item) {
      item.identifySub.trampleShow = false;
      this.$toast({
        message: "意见已收到",
        icon: require("../../assets/images/succeed.png"),
      });
      this.$axios.post("/hopeComments/insertHopeComments", {
        aamid: this.userInfoObj.aamid,
        moduleid: item.moduleid,
        logtime: Date.parse(new Date()),
        comments: 0,
        description: "不感兴趣",
      });
    },
    //section点踩意见
    onTreadIdea() {
      if (this.treadMsg) {
        this.writeShow = false;
        this.treadMsg = "";
        this.$toast({
          message: "提交成功",
          icon: require("../../assets/images/succeed.png"),
        });
        this.$axios.post("/hopeComments/insertHopeComments", {
          aamid: this.userInfoObj.aamid,
          moduleid: this.secConObj.moduleid,
          logtime: Date.parse(new Date()),
          comments: 0,
          description: this.treadMsg,
        });
      } else {
        this.$toast({
          message: "意见不能为空",
          icon: require("../../assets/images/fail.png"),
        });
      }
    },
    //点击section跳转增加足迹
    onSectionView(item) {
      window.location.href = item.url;
      if (item.updateShow === true) {
        instance.get(
          `/xxtx/py/add-mark?aamid=${this.userInfoObj.aamid}&moduleid=${item.moduleid}&modulename=${item.title}`
        );
      }
      this.$axios.post("/hopeUserLog_h/insertUserLog_h", {
        aamid: this.userInfoObj.aamid,
        moduleid: item.moduleid,
        logtime: Date.parse(new Date()),
      });
    },
    //更新红点
    getDataUpdate(arr,num){
      instance.get(`/xxtx/py/reminder?aamid=${this.userInfoObj.aamid}`)
      .then((res) => {
        if (res.data.errcode == "success" && res.status === 200) {
          var updateArr = []
          updateArr = [...res.data.data] || [];
          updateArr.forEach((i) => {
            arr.forEach((item) => {
              if (i === item.moduleid) {
                item.updateShow = true;
              }
            });
          });
          if(num==1){
            setTimeout(()=>{
              this.secTabContentAll = arr;
              this.secTabContentAllOne = this.secTabContentAll
            },0)
          } 
        } 
      })
    },
    //获取section数据
    getSectionView() {
      this.$axios
        .post("/hopeModule/searchMoudleByGroup", {
          aamid: this.userInfoObj.aamid,
          deptid: this.userInfoObj.deptid,
          odeptid: this.userInfoObj.odeptid,
        })
        .then((res) => {
          if (res.status === 200 && res.data.status === "000000") {
            let dataAll = [...res.data.data.allModules]
            let dataTab = [...res.data.data.modulegroupSet]
            this.getDataUpdate(dataAll,1)
            // this.secTabContentAll = dataAll;
            // this.secTabContentAllOne = this.secTabContentAll
            this.secTabContent = dataTab;
            this.secTabContent.unshift("全部");
            this.tabSection = res.data.data.mstatus;
            const moduleidList = Array.isArray(dataAll)
              ? dataAll.map((i) => i.moduleid)
              : [];
            this.$store.commit("setModuleidList", moduleidList);
            if (JSON.parse(sessionStorage.getItem("recommendFlag")) == true) {
              return;
            } else {
              this.viewRecommend(); //视图推荐
              sessionStorage.setItem("recommendFlag", JSON.stringify(true));
            }
          }
        });
    },
    //点击视图tab li切换
    onSectionViewTab(item, index) {
      this.secTabActive = index;
      this.secTabContentAll.forEach((i) => {
        i.identifySub.trampleShow = false;
      });
      if (this.tabSection == true) {
        this.secTabConTop =
          document.querySelector("#secTabCon").offsetTop - 100;
      } else {
        this.secTabConTop =
          document.querySelector("#secTabCon1").offsetTop - 100;
      }
      if (this.scrollTop >= this.secTabConTop) {
        document.documentElement.scrollTop = this.secTabConTop;
        document.body.scrollTop = this.secTabConTop;
      }
      if (index === 0) {
        this.secTabContentAllOne = this.secTabContentAll;
      } else {
        this.secTabContentAllOne = this.secTabContentAll.filter((i) => {
          return i.modulegroupname == item;
        });
      }
    },
    //点击切换sec视图
    onSecTabView() {
      this.tabSection = !this.tabSection;
      this.secTabContentAll.forEach((i) => {
        i.identifySub.trampleShow = false;
      });
      this.$axios.post("/hopeModule/editModuleConf", {
        aamid: this.userInfoObj.aamid,
        confvalue: this.tabSection === true ? 0 : 1,
        setname: "分组",
      });
    },
  },
};
